  text-align: center;
  color: #2c3e50;
  margin-top: 60px; */
  /* max-width: 1920px; */
  margin: 0 auto;
  min-width: 320px;
}

.home{
  background: #dbdbdb;
  width: 100%;
  height: 100vh;
}
.content{
  border-radius: 8px;
  /* width: 90%; */
  max-width: 900px;
  background: #ffffff;
  -webkit-box-shadow: 0px 0px 8px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 8px 3px rgba(0, 0, 0, 0.2);
}
.tab-btn,.cancel-btn{
  border: none;
  color: #ffa600;
}
.tab-btn-checked,.tab-btn:focus,.tab-btn:active,.cancel-btn:focus,.cancel-btn:active{
  border: none;
  outline: none;
  background: #ffa600;
  color: #ffffff;
}
.pointType-btn{
  border: none;
  background: #dbdbdb;
  color: #7c7c7c;
  font-weight: 900;
}
.pointType-btn-checked,.pointType-btn:focus,.pointType-btn:active{
  border: none;
  outline: none;
  background: #efefef;
  color: #ffa600;
}
.keyboard-btn{
  width: calc(33% - 2px);
  text-align: center;
  background: rgb(255, 255, 255);
  border: 1px solid #ffa600;
  border-radius: 8px;
  margin: 2px;
  color: #ffa600;
  cursor: pointer;
  padding: 8px;
  font-size: 24px;
  font-weight: 800;
}
.keyboard-frame div:nth-child(4) .keyboard-btn:nth-child(3){
  color: #ffffff;
  background: rgb(255, 166, 0);
}
.keyboard-frame div:nth-child(4) .keyboard-btn:nth-child(1){
  color: #ffffff;
  background: #c0c0c0;
  border: 2px solid #c0c0c0;
}
.input-btn,.input-btn:focus,.input-btn:active{
  width: 100%;
  height: 40px;
  border-radius: 12px;
  border: 2px solid #c0c0c0;
  padding: 6px;
  font-size: 18px;
  color: #888888;
  background: #ffffff;
}
.input-btn-border,.input-btn-border:focus,.input-btn-border:active {
    height: 40px;
    border-radius: 12px;
    padding: 6px;
    font-size: 18px;
    border: 2px solid #f6aa00;
    outline: none;
    -webkit-box-shadow: 0px 0px 8px 1px rgba(0,0,0, 0.3);
            box-shadow: 0px 0px 8px 1px rgba(0,0,0, 0.3);
}
.cancel-btn{
  background: rgb(255, 166, 0);
  color: #ffffff;
  font-weight: 800;
  border-radius: 8px;
  padding: 4px 8px;
}
.cancel-btn:disabled{
  background: rgb(173, 173, 173);
}
.table-frame{
  overflow-x: scroll;
}
.table tbody td, .table tbody th {
  vertical-align: middle;
  padding: 8px 4px;
}

